import {
	Divider,
	Link,
	createStyles,
	makeStyles,
	Button,
	SwipeableDrawer,
	List,
	ListItem,
	IconButton,
	Box,
} from "@material-ui/core";
import { AngloAmericanLogo } from "./AngloAmericanLogo";
import { VoxelLogo } from "./VoxelLogo";
import Grid from "@material-ui/core/Grid";
import CloseIcon from "@material-ui/icons/Close";
import MenuIcon from "@material-ui/icons/Menu";
import React, { useState } from "react";

const useStyles = makeStyles((theme) =>
	createStyles({
		angloAmericanLogo: {
			marginLeft: 24,
		},
		hyperlink: {
			opacity: "0.99",
			color: "#002776",
			fontFamily: "Arial",
			fontSize: 16,
			fontWeight: "bold",
			textAlign: "center",
			paddingRight: "24px",
		},
		headerDivider: {
			marginBottom: 64,
			[theme.breakpoints.down("xs")]: {
				display: "none",
			},
		},
		mobileHeaderDivider: {
			[theme.breakpoints.up("sm")]: {
				display: "none",
			},
		},
		voxelLogoContainer: {
			[theme.breakpoints.down("xs")]: {
				textAlign: "center",
			},
		},
		siteHeaderLinks: {
			[theme.breakpoints.down("xs")]: {
				display: "none",
			},
		},
		siteHeaderLinksBurgerMenu: {
			[theme.breakpoints.up("sm")]: {
				display: "none",
			},
		},
		appDrawer: {
			width: "80%",
		},
		siteHeader: {
			[theme.breakpoints.down("xs")]: {
				position: "sticky",
				top: 0,
				background: "white",
				zIndex: 999,
			},
		},
	})
);

interface HeaderLinkItem {
	text: string;
	link: string;
}

export const SiteHeader = () => {
	const [isDrawerOpen, setDrawerOpen] = useState(false);
	const classes = useStyles();

	const toggleDrawer = (open: boolean) => () => {
		setDrawerOpen(open);
	};

	const headerLinks: HeaderLinkItem[] = [
		{
			text: "About",
			link: "#whatIsVoxel",
		},
		{
			text: "Training",
			link: "#prepareForChange",
		},
		{
			text: "Find out more",
			link: "#p3processing",
		},
	];

	const onAppDrawerItemClick = () => {
		setDrawerOpen(false);
	};

	return (
		<>
			<Grid
				className={classes.siteHeader}
				container
				justify="space-between"
				item
			>
				<Grid
					container
					spacing={2}
					alignItems="center"
					justify="flex-start"
					item
					sm={6}
				>
					<Grid item xl={3} lg={4} md={5} sm={6} xs={10}>
						<AngloAmericanLogo className={classes.angloAmericanLogo} />
					</Grid>
					<Grid item xs={2} className={classes.siteHeaderLinksBurgerMenu}>
						<Button onClick={toggleDrawer(true)}>
							<MenuIcon />
						</Button>
						<SwipeableDrawer
							classes={{
								paper: classes.appDrawer,
							}}
							anchor="right"
							open={isDrawerOpen}
							onClose={toggleDrawer(false)}
							onOpen={toggleDrawer(true)}
						>
							<Box display="flex" alignItems="center" justifyContent="flex-end">
								<IconButton onClick={toggleDrawer(false)}>
									<CloseIcon />
								</IconButton>
							</Box>
							<Divider />
							<List>
								{headerLinks.map((headerLink) => (
									<ListItem
										button
										key={headerLink.text}
										onClick={onAppDrawerItemClick}
									>
										<Link className={classes.hyperlink} href={headerLink.link}>
											{headerLink.text}
										</Link>
									</ListItem>
								))}
							</List>
						</SwipeableDrawer>
					</Grid>
					<Grid className={classes.mobileHeaderDivider} item xs={12}>
						<Divider />
					</Grid>
					<Grid className={classes.voxelLogoContainer} item xl lg md sm xs={12}>
						<Link href={"/"}>
							<VoxelLogo />
						</Link>
					</Grid>
				</Grid>
				<Grid
					className={classes.siteHeaderLinks}
					container
					alignItems="center"
					justify="flex-end"
					item
					sm={6}
				>
					{headerLinks.map((headerLink) => (
						<Grid key={headerLink.text} item>
							<Link className={classes.hyperlink} href={headerLink.link}>
								{headerLink.text}
							</Link>
						</Grid>
					))}
				</Grid>
			</Grid>

			<Grid className={classes.headerDivider} item md={12} sm={12} xs={12}>
				<Divider />
			</Grid>
		</>
	);
};
