const isNullOrUndefined = (source) => source !== null && source !== undefined;

const useIsInDraftMode = () => {
    return isNullOrUndefined(process) &&
        isNullOrUndefined(process.env) &&
        isNullOrUndefined(process.env.REACT_APP_VOXEL_MODE) &&
        process.env.REACT_APP_VOXEL_MODE === "DRAFT";
}

export default useIsInDraftMode;
