import React, {ReactChild, ReactFragment, ReactPortal} from "react";

import {
    Box,
    CssBaseline,
    createMuiTheme,
    makeStyles,
    createStyles,
    ThemeProvider,
} from "@material-ui/core";

import Grid from "@material-ui/core/Grid";

import "./../global.css"

import { SiteHeader } from "../components/SiteHeader";
import {SiteFooter} from "./SiteFooter";

export const useStyles = makeStyles((theme) =>
    createStyles({
        root: {
            flexGrow: 1,
        },
        contentContainer: {
            [theme.breakpoints.down("md")]: {
                paddingLeft: "32px",
                paddingRight: "32px",
            },
            [theme.breakpoints.down("sm")]: {
                paddingLeft: "16px",
                paddingRight: "16px",
            },
            [theme.breakpoints.down("xs")]: {
                paddingLeft: "8px",
                paddingRight: "8px",
            },
        },
    })
);

export type ReactNodeWitchChildren = ReactChild | ReactFragment | ReactPortal | boolean | null | undefined;

interface IProps {
    children: ReactNodeWitchChildren
    banner?: any
}

const Layout = ({children, banner}:IProps) => {
    const classes = useStyles();
    const theme = createMuiTheme({});
  
    return (
        <>
            <CssBaseline />
            <ThemeProvider theme={theme}>
                <Box className={classes.root}>
                    <Grid container>
                        <Box marginLeft="auto" marginRight="auto" width="1140px">
                            <Box className={classes.contentContainer}>
                                <SiteHeader />
                                {banner && banner()}
                                {children}
                                <SiteFooter />
                            </Box>
                        </Box>
                    </Grid>
                </Box>
            </ThemeProvider>
        </>
    );
};

export default Layout;
